import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Paper from '@mui/material/Paper';
import { PreviewOrder } from './../order/components/PreviewOrder';
import { getOrderStatus } from "./../../services/login";
import StorageInstance from "../../utils/storage";

import "./TrackShipment.scss";

const steps = [
    'Saved',
    'Placed',
    'Picked',
    'InTransit',
    'Delivered',
];

function TrackShipment() {
    const navigate = useNavigate();
    const [trackingId, setTrackingId] = useState("");
    const [orderStatus, setOrderStatus] = useState(null);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (StorageInstance.isUserLoggedIn()) {
            navigate("/home");
        }
    },[]);

    const gotoLogin = () => {
        navigate("/");
    }

    const onChangeTrackingId = (event) => {
        setTrackingId(event.target.value);
    }

    const getShipmentStatus = async () => {
        // TODO: Validations if any.
        setLoading(true);
        console.log("Tracking ID:", trackingId);
        const { data } = await getOrderStatus(trackingId);
        console.log("Response:", data);
        setOrderStatus(data);
        setTimeout(() => {
            setLoading(false);
        }, 2000);
    }

    return(
        <div className="tracking-page">
            <div className="header">
                <Button color="warning" onClick={gotoLogin}>Login</Button>
            </div>
            <div className="content">
                <div className="search-container">
                    <TextField required label="Tracking ID" onChange={onChangeTrackingId}/>
                    <Button color="primary" variant="contained" onClick={getShipmentStatus}>Search</Button>
                </div>
            </div>
            
                { /* TRACKING DETAILS */}
                { orderStatus && <Paper elevation={1} style={{ margin: 24, padding: 20 }} >
                        <div>
                            PACKAGE STATUS
                        </div>
                    
                        {
                            <Stepper activeStep={orderStatus.status} alternativeLabel>
                                {steps.map((label) => (
                                <Step key={label}>
                                    <StepLabel>{label}</StepLabel>
                                </Step>
                                ))}
                            </Stepper>
                        }

                        <div style={{ marginTop: 30 }}>
                            PACKAGE DETAILS
                        </div>

                        <div className="package-details">
                            { orderStatus && <PreviewOrder {...orderStatus} /> }
                        </div>
                    </Paper>
                }

            { /* LOADING INDICATORS */}
            { loading && 
                <Backdrop
                    sx={{ color: '#FFFFFF', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                    open={true}
                    onClick={() => {}}>
                    <CircularProgress color="inherit" />
                </Backdrop>
            }
        </div>
    );
}
export default TrackShipment;