import React, {useState, useEffect} from 'react';
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import Backdrop from '@mui/material/Backdrop';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';

const addressDetailValidations = {
    companyName: 'Enter Company name.',
    name: 'Enter Name.',
    position: 'Enter Poistion.',
    addressLine1: 'Enter Address Line 1',
    addressLine2: 'Enter Address Line 2',
    postalCode: 'Enter Postal Code.',
    phoneNumber: 'Enter Phone Number.',
    faxNumber: 'Enter FAX number.',
    uenNumber: 'Enter UEN Number.',
    email: 'Enter Email Address.',
};

export const AddressInfo = React.forwardRef((props, ref) => {
    const { header } = props;
    const [addressDetails, setAddressDetails] = useState({});
    useEffect(() => {
        if (props?.defaultAddress) {
            setAddressDetails({ ...props.defaultAddress });
        }
    },[props?.defaultAddress]);

    React.useImperativeHandle(ref, () => ({
        doValidation: doValidation,
        getAddressDetails: getAddressDetails
      }));

    const onAddressDetailsChange = (event) => {
        let { name, value } = event.target;
        setAddressDetails({ ...addressDetails, [name]: value });
    }

    const getAddressDetails = () => {
        console.log('2. Company Address :', addressDetails);
        return addressDetails;
    }

    const doValidation = () => {
        let canContinue = true;

        Object.keys(addressDetailValidations).forEach((key, index) => {
            if ((!addressDetails[key]) || addressDetails[key] === "") {
                setAddressDetails(prevState => ({
                    ...prevState, [key]: "", error: true,
                    errorMessage: { ...prevState.errorMessage, [key]: addressDetailValidations[key] }
                }));
                canContinue = false;
            }
        });

        return canContinue;
    }


    return (
        <div style={{ display: 'flex', justifyContent: 'center'  }}>
            <div className="address-details" style={{ width: '30vw', marginBottom: '20px' }}>
                <Typography variant="caption" display="block" gutterBottom style={{color: '#072753', fontWeight: '600' }}>
                    {header}
                </Typography>
                <div className="form-group">
                    <TextField label="Company Name *" name="companyName" margin="dense" size="small" fullWidth  InputLabelProps={{ shrink: addressDetails?.companyName ? true:false }} 
                        error={addressDetails?.companyName === ""} value={addressDetails?.companyName || "" } onChange={onAddressDetailsChange} /><br />

                    <TextField label="Name *" name="name" margin="dense" size="small" fullWidth 
                        error={addressDetails?.name === ""} value={addressDetails?.name || ""} onChange={onAddressDetailsChange} /><br />
                    <TextField label="Position *" name="position" margin="dense" size="small" fullWidth
                        error={addressDetails?.position === ""} value={addressDetails?.position || ""} onChange={onAddressDetailsChange} /><br />
                    <TextField label="Address Line 1 *" name="addressLine1" margin="dense" size="small" fullWidth
                        error={addressDetails?.addressLine1 === ""} value={addressDetails?.addressLine1  || ""} onChange={onAddressDetailsChange} /><br />
                    <TextField label="Address Line 2 " name="addressLine2" margin="dense" size="small" fullWidth
                        error={addressDetails?.addressLine2 === ""} value={addressDetails?.addressLine2  || ""} onChange={onAddressDetailsChange} /><br />
                    <TextField label="Postal Code *" name="postalCode" margin="dense" size="small" fullWidth 
                        error={addressDetails?.postalCode === ""} value={addressDetails?.postalCode  || ""} onChange={onAddressDetailsChange} /><br />
                    <TextField label="Phone Number *" name="phoneNumber" margin="dense" size="small" fullWidth 
                        error={addressDetails?.phoneNumber === ""} value={addressDetails?.phoneNumber  || ""} onChange={onAddressDetailsChange} /><br />
                    <TextField label="FAX NO *" name="faxNumber" margin="dense" size="small" fullWidth 
                        error={addressDetails?.faxNumber === ""} value={addressDetails?.faxNumber  || ""} onChange={onAddressDetailsChange} /><br />
                    <TextField label="UEN Number *" name="uenNumber" margin="dense" size="small" fullWidth 
                        error={addressDetails?.uenNumber === ""} value={addressDetails?.uenNumber || ""} onChange={onAddressDetailsChange} /><br />
                    <TextField label="Email *" name="email" margin="dense" size="small" fullWidth 
                        error={addressDetails?.email === ""} value={addressDetails?.email  || ""} onChange={onAddressDetailsChange} /><br />
                </div>
            </div>
        </div>
    );

});