import { ProSidebar, Menu, MenuItem, SubMenu, SidebarHeader, SidebarContent, SidebarFooter } from 'react-pro-sidebar';
import 'react-pro-sidebar/dist/css/styles.css';
import DashboardRoundedIcon from '@mui/icons-material/DashboardRounded';
import AssignmentRoundedIcon from '@mui/icons-material/AssignmentRounded';
import InsertChartRoundedIcon from '@mui/icons-material/InsertChartRounded';
import AddBoxRoundedIcon from '@mui/icons-material/AddBoxRounded';
import ListAltRoundedIcon from '@mui/icons-material/ListAltRounded';
import GroupRoundedIcon from '@mui/icons-material/GroupRounded';
import { useNavigate } from 'react-router-dom';
import { CONSTANTS } from './../../utils/constants';
import React from 'react';
import {ClientMenu, ClientMenuRoute} from './../../utils/constants';
import {AdminMenu, AdminMenuRoute } from './../../utils/constants';
import StorageInstance from "./../../utils/storage";

const ClientMenuIcon = {
    Report: <InsertChartRoundedIcon />,
    NewOrder: <AddBoxRoundedIcon />,
    ShipmentStatus: <ListAltRoundedIcon />,
    Account: <AssignmentRoundedIcon />
};

const AdminMenuIcon = {
    Dashboard: <InsertChartRoundedIcon />,
    OrderList: <ListAltRoundedIcon />,
    UserList: <GroupRoundedIcon />,
};

const SideNav = () => {
    const navigate = useNavigate();
    // TODO: 
    // 1. Store user profile after login
    // 2. Get the value from storage.
    const profileData = JSON.parse(StorageInstance.getItem(CONSTANTS.userData));
    const roleId = profileData.RoleNum;
    console.log('RoleId in Side Nav:', roleId);

    const getMenuItem = () => {
        if (CONSTANTS.Role.Client === roleId) {
            const menuItemList = Object.keys(ClientMenu).map(menuKey => {
                return <MenuItem key={menuKey} onClick={() => { onMenuClicked(menuKey)}} icon={ClientMenuIcon[menuKey]}>{ClientMenu[menuKey]}</MenuItem>
            });
            
            return menuItemList;
        } else if (CONSTANTS.Role.Admin === roleId) {
            const menuItemList = Object.keys(AdminMenu).map(menuKey => {
                return <MenuItem key={menuKey} onClick={() => { onMenuClicked(menuKey)}} icon={AdminMenuIcon[menuKey]}>{AdminMenu[menuKey]}</MenuItem>
            });
            
            return menuItemList;
        } else {
            navigate('/');
        }
    }

    const onMenuClicked = (menuKey) => {
        console.log("Menu Clicked:", menuKey);
        if (CONSTANTS.Role.Client === roleId) {
            navigate(ClientMenuRoute[menuKey]);
        } else if (CONSTANTS.Role.Admin === roleId) {
            navigate(AdminMenuRoute[menuKey]);
        } else {
            navigate('/');
        }
    }

    return (
        <ProSidebar style={{ boxShadow: '2px 0px 10px 0px #939495' }} collapsed={false}>
            <SidebarHeader style={{ height: '60px' }}>
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: 'inherit' }}>
                    MATRIX
                </div>
            </SidebarHeader>
            <SidebarContent>
                <Menu iconShape="square">
                    {
                        getMenuItem()
                    }
                    {/* <MenuItem icon={<DashboardRoundedIcon />}>Dashboard</MenuItem>
                    <MenuItem icon={<AssignmentRoundedIcon />}>Order</MenuItem> */}

                    {/* <SubMenu title="Order" icon={<AssignmentRoundedIcon />}>
                        <MenuItem icon={<ArticleRoundedIcon />}>Order List</MenuItem>
                        <MenuItem icon={<AddBoxRoundedIcon />}>Add New Order</MenuItem>
                    </SubMenu> */}
                </Menu>
            </SidebarContent>
            <SidebarFooter style={{ height: '60px' }}>
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: 'inherit', fontSize: '10px' }}>
                    copyright © matrix tech 2022.
                </div>
            </SidebarFooter>
        </ProSidebar>
    );
};

export default SideNav;