import { Login, Logout, orderStatus } from "./mock";

// AxiosInstance.post('/api/login', { email: email, password: password });

const SignInWithPassword = async (email, password) => {
    try {
        const requestBody = { LoginID: email, Password: password };
        // const { data, status } = await AxiosInstance.post(URL.LOGIN, requestBody);
        // return { data, status };
        const data = await Login({ username: email, password });
        return { data };
    } catch (error) {
        // This is the main part. Use the response property from the error object
        console.log("Error: ", error.response);
        return error.response;
    }
};

const LogoutUser = async (userId, sessionId) => {
    try {
        const requestBody = { LoginID: userId, Email: sessionId };
        //  const { data, status } = await AxiosInstance.post(URL.LOGOUT, requestBody);
        // return { data, status };
        const data = await Logout();
        return { data };
    } catch (error) {
        // This is the main part. Use the response property from the error object
        console.log("Error: ", error.response);
        return error.response;
    }
};


