// NOTE: Assumption is that session storage is present in the browser
import { CONSTANTS }  from "./constants";

const Storage = () => {
    const isUserLoggedIn = () => {
        return sessionStorage.getItem(CONSTANTS.isLoggedIn) === 'true';
    }

    const addItem = (itemKey, value) => {
        sessionStorage.setItem(itemKey, value);
    }

    const getItem = (itemKey) => {
        return sessionStorage.getItem(itemKey);
    }

    const removeItem = (itemKey) => {
        sessionStorage.removeItem(itemKey);
    }

    return { isUserLoggedIn, addItem, getItem, removeItem };
}

const StorageInstance = Storage();

export default StorageInstance;
