import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import StorageInstance from "./../../../utils/storage";

import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import StepContent from '@mui/material/StepContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

import AddressDetails from './../components/AddressDetails';
import PackageDetails from './../components/PackageDetails';
import { PreviewOrder } from './../components/PreviewOrder';
import { NewOrder } from './../../../services/order';

const steps = ['Shipper & Consignee Details', 'Package Details', 'Confirmation'];

export default function HorizontalLinearStepper() {
  const [loading, setLoading]=useState(false);
  const [senderDetails, setSenderDetails] = useState({});
  const [receiverDetails, setReceiverDetails] = useState({});
  const [itemList, setItemList] = useState([]);
  const [isDocument, setIsDocument] = useState(false);
  const [currency, setCurrency] = useState('');

  const [activeStep, setActiveStep] = React.useState(0);
  const [openSuccessPopup, setOpenSuccessPopup] = React.useState(false);
  const [openDiscardModal, setOpenDiscardModal] = React.useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    if (!StorageInstance.isUserLoggedIn()) {
        navigate("/");
    }
  },[]);

  // -> Modal
  const handleDiscardModalOpen = () => {
    setOpenDiscardModal(true);
  };

  const handleDiscardModalClose = () => {
    setOpenDiscardModal(false);
  };
  // <- Modal

  const onClickStepper = (step) => {
    if (step <= activeStep) {
      setActiveStep(step);
    }
  }

  const onSaveAndContinueAddress = ({ senderDetails, receiverDetails }) => {
    setSenderDetails(senderDetails);
    setReceiverDetails(receiverDetails);
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  }

  const onSaveAndContinueItem = ({ itemList, isDocument }) => {
    setItemList(itemList);
    setIsDocument(isDocument);
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  }

  const handleSaveOrder = async () => {
    // TODO:
    // 1. Make API call with all the details
    // 2. Show success message with two CTA (Order List/Create New)
    // 3. Handle all possible scenarios.
    setLoading(true);
    const { data } = await NewOrder(senderDetails, receiverDetails, itemList, currency, isDocument, "SAVE");
    console.log("New Order Response:", data);
    setLoading(false);
    setOpenSuccessPopup(true);
  };

  const handleSumbitOrder = async () => {
    // TODO:
    // 1. Make API call with all the details
    // 2. Show success message with two CTA (Order List/Create New)
    setLoading(true);
    const { data } = await NewOrder(senderDetails, receiverDetails, itemList, currency, isDocument,  "SUBMIT");
    console.log("New Order Response:", data);
    setLoading(false);
    setOpenSuccessPopup(true);
  }

  const handleSuccessPopupClose = (event, reason) => {
    if (reason && reason == "backdropClick") 
        return;
    setOpenSuccessPopup(false);
  }

  const onCancelOrder = () => {
    handleDiscardModalOpen();
  }

  const goToOrderList = () => {
    navigate("/home/order");
    setOpenSuccessPopup(false);
  }

  const createNewOrder = () => {
    setOpenSuccessPopup(false);
    setActiveStep(0);
    setSenderDetails({});
    setReceiverDetails({});
    setItemList([]);
  }

  return (
    <React.Fragment>
      { /* Stepper Tilte */}
      <Paper style={{ display: 'flex', justifyContent: 'space-between', flexDirection: 'row', marginBottom: '24px', padding: '10px 20px' }}>
        <Typography variant="overline" display="block" gutterBottom style={{color: '#072753', fontWeight: '600' }}>
          Create Order
        </Typography>
        <Button variant="outlined" startIcon={<ArrowBackIosIcon />} onClick={onCancelOrder} >
          Back
        </Button>
      </Paper>

      { /* Stepper with Content */}
      <Paper sx={{ padding: '24px', maxHeight: 'calc(100vh - 180px)', overflowY: 'scroll' }}>
        <Stepper activeStep={activeStep} orientation="vertical">
            { steps.map((label, index) => {
              return (
                  <Step key={label}>
                      <StepLabel onClick={(e) => { onClickStepper(index) }}>{label}</StepLabel>
                      <StepContent>
                        { activeStep === 0 ? <AddressDetails sender={senderDetails} receiver={receiverDetails} 
                          saveAndContinueAddress={onSaveAndContinueAddress}/> : null }
                        { activeStep === 1 ? <PackageDetails items={itemList} saveAndContinueItem={onSaveAndContinueItem} isDocument={isDocument} /> : null }
                      </StepContent>
                  </Step>
              );
            })}
        </Stepper>

        { /* FINAL Bottom Actions */}
        {activeStep === (steps.length -1 ) && (
            <React.Fragment>
                <Typography sx={{ mt: 2, mb: 1 }}>
                    <PreviewOrder senderDetails={senderDetails} receiverDetails={receiverDetails} itemDetails={itemList} isDocument={isDocument} 
                      selectedCurrency={currency} onCurrencyChange={(selectedCurrencyCode) => {
                        setCurrency(selectedCurrencyCode);
                      }}/>
                </Typography>
                <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                    <Box sx={{ flex: '1 1 auto' }} />
                    <Button variant="outlined" onClick={handleSaveOrder} style={{ marginRight: '20px'}}> Save </Button>
                    <Button variant="contained" onClick={handleSumbitOrder}> Submit </Button>
                </Box>
            </React.Fragment>
        )}

        { /* Discard changes popup */}
        <Dialog
            open={openDiscardModal}
            onClose={handleDiscardModalClose}>

            <DialogTitle style={{ padding: '30px' }}>
                {"Discard Changes"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    Changes will not be saved. Do you want to proceed?
                </DialogContentText>
            </DialogContent>
            <DialogActions style={{ padding: '30px' }}>
                <Button onClick={handleDiscardModalClose} autoFocus>Cancel</Button>
                <Button variant="contained" onClick={goToOrderList}>Discard</Button>
            </DialogActions>
        </Dialog>
      </Paper>


      { /* SUCCESS MODAL */}
        <Dialog
            open={openSuccessPopup}
            onClose={handleSuccessPopupClose}>
            <DialogTitle style={{ display: 'flex', justifyContent: 'center', padding: '30px' }}>
                <CheckCircleIcon style={{ width: '64px', height: '64px', color: '#188b18' }}/>
            </DialogTitle>
            <DialogContent style={{ display: 'flex', justifyContent: 'center' }}>
                <DialogContentText>
                    Order created successfully.
                </DialogContentText>
            </DialogContent>
            <DialogActions style={{ padding: '30px' }}>
                <Button variant="contained" onClick={goToOrderList}>Go To Order List</Button>
                <Button variant="contained" onClick={createNewOrder} autoFocus>Create New Order</Button>
            </DialogActions>
        </Dialog>

        { loading && 
            <Backdrop
                sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                open={true}
                onClick={() => {}}>
                <CircularProgress color="inherit" />
            </Backdrop>
        }
    </React.Fragment>
  );
}







