import React, { useEffect, useState } from 'react';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import AddIcon from '@mui/icons-material/Add';
import { useNavigate } from 'react-router-dom';
import StorageInstance from "./../../utils/storage";
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import EditRoundedIcon from '@mui/icons-material/EditRounded';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import RemoveRedEyeRoundedIcon from '@mui/icons-material/RemoveRedEyeRounded';
import CloseIcon from '@mui/icons-material/Close';
import PrintRoundedIcon from '@mui/icons-material/PrintRounded';
import Pagination from '@mui/material/Pagination';
import InfoIcon from '@mui/icons-material/Info';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import { GetAllOrders, GetOrder, CancelOrder } from "./../../services/order";

import './OrderList.scss';
import { PrintPreview } from './PrintPreview';

const OrderList = () => {
  const navigate = useNavigate();
  const [orderList, setOrderList]= useState([]);
  const [selectedOrder, setSelectedOrder]= useState({});
  const [loading, setLoading]=useState(false);
  const [openPreviewPopup, setOpenPreviewPopup]=useState(false);
  const [selectedOrderId, setSelectedOrderId]= useState(null);
  const [openCancelModal, setOpenCancelModal]=useState(false);
  const [refresh, setRefresh]=useState(false);

  useEffect(() => {
    if (!StorageInstance.isUserLoggedIn()) {
        navigate("/");
    }
  },[]);

  useEffect(async () => {
    setLoading(true);
    const { data } = await GetAllOrders();
    console.log("OrderList Response:", data);
    setOrderList(data.Results);
    setLoading(false);
  },[refresh]);

  const onNewOrder = () => {
    navigate("/home/neworder");
  }

  const onViewOrder = async (orderId) => {
    setLoading(true);
    const { data } = await GetOrder(orderId);
    console.log("onViewOrder Response:", data);
    setSelectedOrder(data.Results);
    setLoading(false);
    setOpenPreviewPopup(true);
  }

  const onPrintOrder = async (orderId) => {
    // TODO: Need to create new page for this.
    setLoading(true);
    const { data } = await GetOrder(orderId);
    console.log("onViewOrder Response:", data);
    setSelectedOrder(data.Results);
    setLoading(false);
    setOpenPreviewPopup(true);
    window.print();
    setOpenPreviewPopup(false);
  }

  const onContinueCancelOrder = async () => {
    setLoading(true);
    const { data } = await CancelOrder(selectedOrderId);
    console.log("onViewOrder Response:", data);
    setLoading(false);
    setOpenCancelModal(false);
    setRefresh(!refresh);
  }
  const onCancelOrder = (orderId) => {
    setSelectedOrderId(orderId)
    setOpenCancelModal(true);
  }

  return (
    <div>
      <Paper style={{ display: 'flex', justifyContent: 'space-between', flexDirection: 'row', marginBottom: '24px', padding: '10px 20px' }}>
        <Typography variant="overline" display="block" gutterBottom style={{color: '#072753', fontWeight: '600' }}>
          Order List
        </Typography>
        <Button variant="outlined" startIcon={<AddIcon />} onClick={onNewOrder} >
          New Order
        </Button>
      </Paper>

       { orderList.length > 0 ? <TableContainer component={Paper} className="table-container">
        <Table sx={{ minWidth: 650 }}>
          <TableHead className="table-header">
            <TableRow>
              <TableCell>#ID</TableCell>
              <TableCell align="right">Tracking ID</TableCell>
              <TableCell align="right">Order Status</TableCell>
              <TableCell align="right">Created On</TableCell>
              <TableCell align="right">Contact No</TableCell>
              <TableCell align="right"></TableCell>
            </TableRow>
          </TableHead>
          <TableBody className="table-body">
            {orderList.map((row, index) => (
              <TableRow key={row.ID} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                {/* <TableCell component="th" scope="row"> {row.id} </TableCell> */}
                <TableCell > {index + 1} </TableCell>
                <TableCell align="right">{row.ID}</TableCell>
                <TableCell align="right">{row.status}</TableCell>
                <TableCell align="right">{row.createdOn || ""}</TableCell>
                <TableCell align="right">{row.receiver.phone}</TableCell>
                <TableCell align="right" style={{ padding: '5px'}} className={
                    row.status === "CANCELLED" ? 'cursor-none'  :  'cursor-pointer'}>
                    <RemoveRedEyeRoundedIcon className="action-button" onClick={() => { onViewOrder(row.ID) }}/>
                    {/* <EditRoundedIcon className="action-button" style={{ marginRight: '15px', color: '#0d6efd', cursor: 'pointer', width: '24px' }} 
                      onClick={() => { console.log("Item " + row.id +" edit clicked") }} /> */}
                    <PrintRoundedIcon className="action-button" onClick={() => { onPrintOrder(row.ID) }}/>
                    <CloseIcon className="action-button delete" onClick={() => { onCancelOrder(row.ID) }}/>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer> :
        <div className="order-list-empty-item">
          <div className="content">
            <InfoIcon /> There is no order, Please add new item.
          </div>
        </div>
      }

      { /*  PAGINATION */}

      {/* <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
        <Pagination count={3} variant="outlined" color="secondary" showFirstButton showLastButton
          style={{ marginTop: '15px' }} onChange={(event, pageNo) => { console.log('PageNo:', pageNo);}}/>
      </div> */}

      { /*  Order Preview */}
      <Dialog open={openPreviewPopup} onClose={() => { setOpenPreviewPopup(false) }} className="preview-window" >
            <DialogTitle style={{ display: 'flex', justifyContent: 'flex-end', padding: '30px' }}>
              <span style={{color: '#071f41', fontWeight: 600}}>INVOICE :</span> |||||||||||||||||||
            </DialogTitle>
            <DialogContent style={{ display: 'flex', justifyContent: 'center' }}>
                <DialogContentText>
                   <PrintPreview senderDetails={selectedOrder.sender} receiverDetails={selectedOrder.receiver} itemDetails={selectedOrder.itemList} 
                   isDocument={selectedOrder.isDocument} selectedCurrency={selectedOrder.currency} />
                </DialogContentText>
            </DialogContent>
            <DialogActions style={{ padding: '30px' }}>
                {/* <Button variant="contained" onClick={goToOrderList}>Go To Order List</Button>
                <Button variant="contained" onClick={createNewOrder} autoFocus>Create New Order</Button> */}
            </DialogActions>
        </Dialog>

        { /* Discard changes popup */}
        <Dialog
            open={openCancelModal}>

            <DialogTitle style={{ padding: '30px' }}>
                {"Cancel Order"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    Are you sure, you want to cancel the order?
                </DialogContentText>
            </DialogContent>
            <DialogActions style={{ padding: '30px' }}>
                <Button onClick={() => { setOpenCancelModal(false)}} autoFocus>Close</Button>
                <Button variant="contained" onClick={onContinueCancelOrder}>Continue</Button>
            </DialogActions>
        </Dialog>

      { /*  Page loading indicator */}
      { loading && 
        <Backdrop
            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
            open={true}
            onClick={() => {}}>
            <CircularProgress color="inherit" />
        </Backdrop>
      }
    </div>
  );
}

export default OrderList;