import { useState, useEffect } from "react";
import Backdrop from '@mui/material/Backdrop';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';

import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import InfoIcon from '@mui/icons-material/Info';

import "./PackageDetails.scss";

const packageItemDetailValidations = {
    weight: 'Enter weight.',
    length: 'Enter length.',
    width: 'Enter width.',
    height: 'Enter height.',
    //volume: 'Enter volume.',
    decValue: 'Enter declared value.',
    itemsCount: 'Select no of items.'
};

const documentItemDetailValidations = {
    weight: 'Enter weight in KGs.',
    noOfPiece: 'Enter No of Pieces.'
    // Description
};

const PackageDetails = ({saveAndContinueItem, items, isDocument}) => {

    const [openAddPackageItem, setOpenAddPackageItem] = useState(false);
    const [packageItemDetails, setPackageItemDetails] = useState({});
    const [packageItemList, setPackageItemList] = useState([]);
    const [editPackageItemIndex, setEditPackageItemIndex] = useState(null);

    const [openAddDocumentItem, setOpenAddDocumentItem] = useState(false);
    const [documentItemDetails, setDocumentItemDetails] = useState({});
    // TODO: Need to fix when navigation between steps
    const [documentItemList, setDocumentItemList] = useState([]);
    const [editDocumentItemIndex, setEditDocumentItemIndex] = useState(null);

    const [showDocumentInfo, setShowDocumentInfo] = useState(false);

    useEffect(() => {
        // TODO: Preset value between steps.
        if (isDocument) {
            setDocumentItemList(items);
            setShowDocumentInfo(true);
            setParcelType('document');
        } else {
            setPackageItemList(items);
            setShowDocumentInfo(false);
            setParcelType('package');
        }
        return () => {}
    }, [])

    const [courierOption, setCourierOption] = useState('courier');
    const handleCourierOptionChange = (event) => {
        setCourierOption(event.target.value);
      };

    const [parcelType, setParcelType] = useState('package');
    const handleParcelTypeChange = (event) => {
        setParcelType(event.target.value);

        if (event.target.value === 'document') {
            setShowDocumentInfo(true);
        } else {
            setShowDocumentInfo(false);
        }
    };

    const handleInputChangeForPackage = (event) => {
        let { name, value } = event.target;
        setPackageItemDetails({ ...packageItemDetails, [name]: value });
        console.log('Item', packageItemDetails);
    };

    const onAddPackageItem = () => {
        setOpenAddPackageItem(true);
        setPackageItemDetails({});
    }

    const onSavePackageItem = () => {
        let canContinue = true;

        // 1. Validating the item details before saving.
        Object.keys(packageItemDetailValidations).forEach((key, index) => {
            if ((!packageItemDetails[key]) || packageItemDetails[key] === "") {
                setPackageItemDetails(prevState => ({
                    ...prevState, [key]: "", error: true,
                    errorMessage: { ...prevState.errorMessage, [key]: packageItemDetailValidations[key] }
                }));
                canContinue = false;
            }
        });

        // 3. Continue if no errors.
        if (canContinue) {
            if (editPackageItemIndex !== null) {
                // Edit and Save the item 
                let itemArray = [...packageItemList];
                itemArray.splice(editPackageItemIndex, 1);
                setPackageItemList([...itemArray, packageItemDetails]);
                setEditPackageItemIndex(null);
            } else {
                // Saving the new item.
                setPackageItemList([...packageItemList, packageItemDetails]);
            }

            setOpenAddPackageItem(false);
        } else {
            console.log("Fill mandatory item details.");
        }
    }

    const onCancelAddPackageItem = () => {
        setOpenAddPackageItem(false);
    }

    const onEditPackageItem = (itemIndex) => {
        setPackageItemDetails(packageItemList[itemIndex]);
        setEditPackageItemIndex(itemIndex);
        setOpenAddPackageItem(true);
    }

    // TODO: Add the implementation for Document.
    const onAddDocumentItem = () => {
        setOpenAddDocumentItem(true);
        setDocumentItemDetails({});
    }

    const handleInputChangeForDocument  = (event) => {
        let { name, value } = event.target;
        setDocumentItemDetails({...documentItemDetails, [name]: value });
        console.log('Document Item:', documentItemDetails);
    }

    const onSaveDocumentItem = () => {
        let canContinue = true;

        // 1. Validating the item details before saving.
        Object.keys(documentItemDetailValidations).forEach((key, index) => {
            if ((!documentItemDetails[key]) || documentItemDetails[key] === "") {
                setDocumentItemDetails(prevState => ({
                    ...prevState, [key]: "", error: true,
                    errorMessage: { ...prevState.errorMessage, [key]: documentItemDetailValidations[key] }
                }));
                canContinue = false;
            }
        });

        // 2. Continue if no errors.
        if (canContinue) {
            if (editDocumentItemIndex !== null) {
                // Edit and Save the item 
                let itemArray = [...documentItemList];
                itemArray.splice(editDocumentItemIndex, 1);
                setDocumentItemList([...itemArray, documentItemDetails]);
                setEditDocumentItemIndex(null);
            } else {
                // Saving the new item.
                setDocumentItemList([...documentItemList, documentItemDetails]);
            }

            setOpenAddDocumentItem(false);
        } else {
            console.log("Fill mandatory item details.");
        }
    }

    const onCancelAddDocumentItem = () => {
        setOpenAddDocumentItem(false);
    }

    const onEditDocumentItem = (itemIndex) => {
        setDocumentItemDetails(documentItemList[itemIndex]);
        setEditDocumentItemIndex(itemIndex);
        setOpenAddDocumentItem(true);
    }

    const canEnableNext = () => {
        if (showDocumentInfo) {
            return documentItemList.length === 0;
        } else {
            return packageItemList.length === 0;
        }

    }

    const onHandleNext = () => {
        // TODO: Do validations
        const itemList = showDocumentInfo ? documentItemList : packageItemList;
        console.log('savedItems', itemList);
        saveAndContinueItem({ itemList, isDocument: showDocumentInfo });
    }

    return (
        <div>

            <div style={{ margin: 20 }}>
                <FormControl style={{ marginBottom: 24 }}>
                    <FormLabel style={{ fontWeight: 600 }}>Transport Mode</FormLabel>
                    <RadioGroup value={courierOption} onChange={handleCourierOptionChange} row>
                        <FormControlLabel value="courier" control={<Radio />} label="Courier" />
                        <FormControlLabel value="airfreight" control={<Radio />} label="Air Freight" disabled/>
                        <FormControlLabel value="seafreight" control={<Radio />} label="Sea Freight" disabled/>
                    </RadioGroup>
                </FormControl><br />

                <FormControl>
                    <FormLabel style={{ fontWeight: 600 }}>Type of Parcel</FormLabel>
                    <RadioGroup value={parcelType} onChange={handleParcelTypeChange} row>
                        <FormControlLabel value="document" control={<Radio />} label="Document" />
                        <FormControlLabel value="package" control={<Radio />} label="Package"/>
                    </RadioGroup>
                </FormControl>< br/>

                { showDocumentInfo && <div style={{ backgroundColor: '#cce5ff', color: '#02162c', borderRadius: 5, fontSize: 12, display: 'inline-block',
                    padding: 5, paddingLeft: 10, paddingRight: 10 }}>
                    If weight exceeds 2kg then document parcel will be considered as package.
                    </div>
                }< br/>

                {/* <FormControl sx={{ m: 1, minWidth: 120 }} size="small">

                    <FormLabel style={{ fontWeight: 600 }}>Type of Parcel</FormLabel>
                    <Select value={parcelType} onChange={handleParcelTypeChange} >
                        <MenuItem value={'document'}>Document</MenuItem>
                        <MenuItem value={'package'}>Package</MenuItem>
                    </Select>
                </FormControl> */}

                <div style={{ display: 'flex', justifyContent: 'flex-end', marginBottom: 15 }}>
                    { showDocumentInfo ? 
                        <Button variant="contained" startIcon={<AddIcon />} onClick={onAddDocumentItem}> Add Document Item </Button>:
                        <Button variant="contained" startIcon={<AddIcon />} onClick={onAddPackageItem}> Add Package Item </Button> 
                    }
                </div>

                {/* ADDED PACKAGE ITEMS LIST */}
                { !showDocumentInfo &&
                    (packageItemList.length > 0 ?
                        <table className="item-list-table">
                        <thead className="table-header">
                            <tr>
                                <th>S.NO</th>
                                <th>WEIGHT</th>
                                <th>DIMENSION(LxWxH)</th>
                                <th>VOLUME</th>
                                <th>DECLARED VALUE</th>
                                <th>NO.OF ITEMS</th>
                                <th>NOTES</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody className="table-body">
                            {
                                packageItemList.map((item, index)=> {
                                    return <tr>
                                        <td>{index+1}</td>
                                        <td>{item.weight}</td>
                                        <td>{item.length}x{item.width}x{item.height}</td>
                                        <td>{item.volume}</td>
                                        <td>{item.decValue}</td>
                                        <td>{item.itemsCount}</td>
                                        <td>{item.notes}</td>
                                        <td><EditIcon className="edit-item-button" onClick={() => onEditPackageItem(index)}/></td>
                                    </tr> 
                                })
                            }
                        </tbody>
                    </table>:
                    <div className="empty-item">
                        <InfoIcon /> Please add one package item at least.
                    </div>)
                }

                {/* ADDED DOCUMENT ITEMS LIST */}
                { showDocumentInfo && 
                    (documentItemList.length > 0 ?
                    <table className="item-list-table">
                        <thead className="table-header">
                            <tr>
                                <th>S.NO</th>
                                <th>WEIGHT (kg)</th>
                                <th>NO.OF PIECES</th>
                                <th>NOTES</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody className="table-body">
                            {
                                documentItemList.map((item, index)=> {
                                    return <tr>
                                        <td>{index+1}</td>
                                        <td>{item.weight}</td>
                                        <td>{item.noOfPiece}</td>
                                        <td>{item.notes}</td>
                                        <td><EditIcon className="edit-item-button" onClick={() => onEditDocumentItem(index)}/></td>
                                    </tr> 
                                })
                            }
                        </tbody>
                    </table>:
                    <div className="empty-item">
                        <InfoIcon />Please add one document item at least.
                    </div>)
                }

                {/* ADD PACKAGE ITEM POPUP */}
                { openAddPackageItem && 
                <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={true}>
                    <Paper style={{ padding: 30 }}>
                    <div className="item-details-header">ITEM DETAILS</div>
                    <div className="add-item-container">
                        <div className="line-item">
                            <div style={{ display: 'flex', alignItems: 'center' }}>
                                <TextField label="Weight *" name="weight" margin="normal" size="small" className="input-width side-align" onChange={handleInputChangeForPackage}
                                    error={packageItemDetails?.weight === ""}  value={packageItemDetails?.weight}/>

                                <TextField label="Length(cm) *" name="length" margin="normal" size="small" className="input-width-120" onChange={handleInputChangeForPackage}
                                    error={packageItemDetails?.length === ""}  value={packageItemDetails?.length}/>x

                                <TextField label="Width(cm) *"  name="width" margin="normal" size="small" className="input-width-120" onChange={handleInputChangeForPackage}
                                    error={packageItemDetails?.width === ""}  value={packageItemDetails?.width}/>x

                                <TextField label="Height(cm) *" name="height" margin="normal" size="small" className="input-width-120" onChange={handleInputChangeForPackage}
                                    error={packageItemDetails?.height === ""}  value={packageItemDetails?.height}/>
                            </div>
                            <div>
                                {/* <TextField label="Volume *" name="volume" margin="normal" size="small" className="input-width side-align" onChange={handleInputChangeForPackage}
                                    error={packageItemDetails?.volume === ""}  value={packageItemDetails?.volume}/> */}

                                <TextField label="Declared Value *" name="decValue" margin="normal" size="small" className="input-width-150 side-align" onChange={handleInputChangeForPackage}
                                    error={packageItemDetails?.decValue === ""}  value={packageItemDetails?.decValue}/>

                                <TextField label="No Of Items *" name="itemsCount" margin="normal" size="small" select className="input-width-150 side-align" onChange={handleInputChangeForPackage}
                                    error={packageItemDetails?.itemsCount === ""}  value={packageItemDetails?.itemsCount}> 
                                { [1,2,3,4,5].map((option) => (
                                    <MenuItem key={option} value={option}>
                                        {option}
                                    </MenuItem>
                                ))}
                                </TextField>
                            </div>
                        </div>
                        <div className="line-item">
                            <TextField label="Notes" name="notes" margin="normal" size="small" fullWidth multiline rows={4} className="side-align" onChange={handleInputChangeForPackage}
                                value={packageItemDetails?.notes}/>
                        </div>
                    </div>  
                    {/* SAVE and CANCEL Actions */}
                    <div style={{ display: "flex", flexDirection: 'row', justifyContent: 'flex-end', marginBottom: 20, marginTop: 30 }}>
                        <Button variant="outlined" onClick={onCancelAddPackageItem} style={{  marginRight: '30px' }}> Cancel </Button>
                        <Button variant="contained" onClick={onSavePackageItem}> Save </Button>
                    </div>
                    </Paper>
                </Backdrop>}

                {/* ADD DOCUMENT ITEM POPUP */}
                { openAddDocumentItem && 
                <Backdrop sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }} open={true}>
                    <Paper style={{ padding: 30 }}>
                    <div className="item-details-header">ITEM DETAILS</div>
                    <div className="add-item-container">
                        <div className="line-item">
                            <div style={{ display: 'flex', alignItems: 'center' }}>
                                <TextField label="Weight(kg) *" name="weight" margin="normal" size="small" style={{ margin: 10 }} onChange={handleInputChangeForDocument}
                                    error={documentItemDetails?.weight === ""}  value={documentItemDetails?.weight}/>

                                <TextField label="No of Pieces *" name="noOfPiece" margin="normal" size="small" style={{ margin: 10}} onChange={handleInputChangeForDocument}
                                    error={documentItemDetails?.noOfPiece === ""}  value={documentItemDetails?.noOfPiece}/>

                                <TextField label="Notes" name="notes" margin="normal" size="small" fullWidth multiline rows={1} style={{ margin: 10 }} onChange={handleInputChangeForDocument}
                                    value={documentItemDetails?.notes}/>
                            </div>
                        </div>
                    </div>  
                    {/* SAVE and CANCEL Actions */}
                    <div style={{ display: "flex", flexDirection: 'row', justifyContent: 'flex-end', marginBottom: 20, marginTop: 30 }}>
                        <Button variant="outlined" onClick={onCancelAddDocumentItem} style={{  marginRight: '30px' }}> Cancel </Button>
                        <Button variant="contained" onClick={onSaveDocumentItem}> Save </Button>
                    </div>
                    </Paper>
                </Backdrop>}
            </div>

            {/* Handling NEXT */} 
            <div style={{ display: 'flex', justifyContent: 'flex-end', marginTop: '30px' }}>
                <Button variant="contained" endIcon={<NavigateNextIcon />} onClick={onHandleNext} disabled={canEnableNext()}>
                    Next
                </Button>
            </div>
        </div>
    );
}

export default PackageDetails;