import React, {useState, useEffect} from 'react';
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import Backdrop from '@mui/material/Backdrop';
import Link from '@mui/material/Link';
import CircularProgress from '@mui/material/CircularProgress';
import Button from '@mui/material/Button';
import { CONSTANTS, STATUSCODE } from '../../utils/constants';
import StorageInstance from "../../utils/storage";

import { SignInWithPassword } from "./../../services/login";
import './Login.scss';

function Login() {
    const [email,setEmail]= useState("");
    const [password,setPassword]=useState("");
    const [loading, setLoading]=useState(false);
    const [error, setError] = useState(false);
    const navigate = useNavigate();
    
    useEffect(() => {
        // TODO:
        // 1. Show splash screen
        if (StorageInstance.isUserLoggedIn()) {
            navigate("/home");
        }
    },[]);

    const login = async () => {
        setError(false);
        setLoading(true);
        console.log("Before Login:", email,password);
        const { data } = await SignInWithPassword(email,password);
        console.log("After Login:", data);
        setLoading(false);
        if (data && data.ResultCode === STATUSCODE.LOGIN.SUCCESS) {
            // TODO: If possible, use store and use storage before reload.
            StorageInstance.addItem(CONSTANTS.isLoggedIn, true);
            StorageInstance.addItem(CONSTANTS.userData, JSON.stringify(data.Results));
            navigate("/home");
        } else {
            StorageInstance.addItem(CONSTANTS.isLoggedIn, false);
            setError(true);
            setEmail("");
            setPassword("");
        }
    }

    const gotoSignup = () => {
        navigate("/signup");
    }
    const gotoTrackShipment = () => {
        navigate("/track");
    }

    return(
        <div className="login-page">
            <div className="header">
                <Button color="warning" onClick={gotoTrackShipment}>Track Shipment</Button>
            </div>
            <div className="content">
                <div className="login-container">
                    <h1 className="login-title">Welcome to Matrix Logistics</h1>
                    <div className="">
                        <input type="text" placeholder="User ID" value={email} onChange={(e)=>setEmail(e.target.value)}
                            className="form-control"></input>
                        <br />
                        <input type="password" placeholder="Password" value={password} onChange={(e)=>setPassword(e.target.value)}
                            className="form-control"></input>

                        { error && 
                            <Box sx={{ color: 'error.main' }} className="error-box">
                                Invalid Login Credentials</Box>
                        }
                        <div className="action-button">
                            <button className="btn btn-primary" onClick={login} disabled={email === "" || password === ""} 
                                style={{width:"120px", margin:"20px 0px"}}>Login </button>
                        </div>
                        <div className="action-button">
                            <Link component="button" variant="body2" underline="hover" onClick={gotoSignup}> 
                                Register as Partner.
                            </Link>
                        </div>
                        
                    { loading && 
                        <Backdrop
                            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                            open={true}
                            onClick={() => {}}>
                            <CircularProgress color="inherit" />
                        </Backdrop>
                    }
                    </div>
                </div>
            </div>
            {/* <div className="background-shape">
            </div> */}
        </div>
    );
}

export default Login;