const Source = {
    Origin : 1,
    Destination : 2,
    NotRequired: 3
};

const Role = {
    SuperAdmin: 1,
    Admin: 2,
    Client : 3,
    OrderPicker  : 4,
    WarehouseAdmin: 5,
    DeliveryPerson: 6,
};

const users = [
    { id: 1, name: 'Muthu', roleId: Role.SuperAdmin, source: Source.NotRequired,
        username: 'Muthu', password: 'muthu' },
    { id: 2, name: 'Siva 1', roleId: Role.SuperAdmin, source: Source.NotRequired,
        username: 'Siva1', password: 'siva' },
    { id: 3, name: 'Siva 2', roleId: Role.Admin, source: Source.NotRequired,
        username: 'Siva2', password: 'siva' },
    { id: 4, name: 'Babu 1', roleId: Role.Admin, source: Source.NotRequired,
        username: 'Babu1', password: 'babu1' },
    { id: 5, name: 'Babu 2', roleId: Role.Client, source: Source.NotRequired,
        username: 'Babu2', password: 'babu2' },
    { id: 6, name: 'Babu 3', roleId: Role.OrderPicker, source: Source.NotRequired,
        username: 'Babu3', password: 'babu3' },
    { id: 7, name: 'Babu 4', roleId: Role.WarehouseAdmin, source: Source.Origin,
        username: 'Babu4', password: 'babu4' },
    { id: 8, name: 'Babu 5', roleId: Role.WarehouseAdmin, source: Source.Destination,
        username: 'Babu5', password: 'babu5' },
    { id: 9, name: 'Babu 6', roleId: Role.DeliveryPerson, source: Source.NotRequired,
        username: 'Babu6', password: 'babu6' },
];

const storageKey = '_matUsersDB';
const createUsers = () => {
    if (!localStorage.getItem(storageKey)) {
        localStorage.setItem(storageKey, JSON.stringify(users));
        console.log('Users are created.');
    } else {
        console.log('Users are in the storage.');
    }
}

const getUserDetails = ({ username, password }) => {
    const users = localStorage.getItem(storageKey);
    if (!!users) {
        const userList = JSON.parse(users);
        const matchedUser = userList.find(x => x.username === username && x.password === password);
        delete matchedUser['username'];
        delete matchedUser['password'];
        return {...matchedUser};
    } else {
        console.log('Users are not found.');
        return null;
    }
}

export {createUsers, getUserDetails };