import { AxiosInstance } from "./axios-instance";
import StorageInstance from "./../utils/storage";
import { CONSTANTS } from "./../utils/constants";
import { URL } from './url';

// AxiosInstance.post('/api/login', { email: email, password: password });

const getUserProfileInfo = () => {
    const profileData = JSON.parse(StorageInstance.getItem(CONSTANTS.userData));
    return { LoginID: profileData.LoginID, token: profileData.Email, Number: profileData.RoleNum };
}

const GetAllOrders =  async () => {
    try {
        const profileData = getUserProfileInfo();
        const requestBody = { ...profileData };
        console.log("Request: ", requestBody);
        //const { data } = await AxiosInstance.post(URL.GET_ALL_ORDER, requestBody);
        //return { data };

        // MOCK - Start
        let orderList = JSON.parse(localStorage.getItem('orderList'));
        if (orderList === null) {
            orderList = [];
        }

        return { data: {
            "ResultCode":100,
            "Results": orderList
        }};
        // MOCK - End
    } catch (error) {
        console.log("Error: ", error.response);
        return error.response;
    }
};

const GetOrder = async (invoiceNum) => {
    try {
        const profileData = getUserProfileInfo();
        const requestBody = { ...profileData, invoice_value: invoiceNum };
        console.log("Request: ", requestBody);
        //const { data } = await AxiosInstance.post(URL.GET_ORDER, requestBody);
        //return { data };

        // MOCK - Start
        let orderList = JSON.parse(localStorage.getItem('orderList'));
        if (orderList === null) {
            orderList = [];
        }
        const result = orderList.find(order => order.ID === invoiceNum);

        return { data: {
            "ResultCode":100,
            "Results": result
        }};
        // MOCK - End
    } catch (error) {
        console.log("Error: ", error.response);
        return error.response;
    }
};


const NewOrder = async (sender, receiver, itemList, currency, isDocument, status) => {
    try {
        const profileData = getUserProfileInfo();
        const requestBody = { ...profileData, sender, receiver, itemList, currency, isDocument, status};
        console.log("Request: ", requestBody);
        // const { data } = await AxiosInstance.post(URL.NEW_ORDER, requestBody);
        // return { data };

        // MOCK - Start
        let existingOrder = JSON.parse(localStorage.getItem('orderList'));
        if (existingOrder === null) {
            existingOrder = [];
        }
        requestBody["ID"] = 'MTX-' + (existingOrder.length + 1);
        requestBody["createdOn"] = new Date(Date.now()).toLocaleString().split(',')[0];
        existingOrder.push(requestBody);
        localStorage.setItem('orderList', JSON.stringify(existingOrder));

        return { data: {
            "ResultCode":100,
            "Results": {
                ID: requestBody["ID"]
            }
        }};
        // MOCK - End

    } catch (error) {
        console.log("Error: ", error.response);
        return error.response;
    }
};

const UpdateOrder = async (invoiceNum, sender, receiver, itemList) => {
    try {
        const profileData = getUserProfileInfo();
        const requestBody = { ...profileData, invoiceNum, sender, receiver, itemList};
        console.log("Request: ", requestBody);
        const { data } = await AxiosInstance.post(URL.UPDATE_ORDER, requestBody);
        return { data };
    } catch (error) {
        console.log("Error: ", error.response);
        return error.response;
    }
};

const CancelOrder = async (invoiceNum) => {
    try {
        const profileData = getUserProfileInfo();
        const requestBody = { ...profileData, invoice_value: invoiceNum};
        console.log("Request: ", requestBody);
        //const { data } = await AxiosInstance.post(URL.CANCEL_ORDER, requestBody);
        //return { data };

        // MOCK - Start
        let orderList = JSON.parse(localStorage.getItem('orderList'));
        if (orderList === null) {
            orderList = [];
        }
        const result = orderList.map(order => {
            if (order.ID === invoiceNum) {
                order['status'] = "CANCELLED";
            }
            return order;
        });

        localStorage.setItem('orderList', JSON.stringify(result));

        return { data: {
            "ResultCode":100,
            "Results": "Cancelled successfully..."
        }};
        // MOCK - End

    } catch (error) {
        console.log("Error: ", error.response);
        return error.response;
    }
};

const UpdateOrderStatus = async (invoiceNum, status) => {
    try {
        const profileData = getUserProfileInfo();
        const requestBody = { ...profileData, invoice_value: invoiceNum, status};
        const { data } = await AxiosInstance.post(URL.CANCEL_ORDER, requestBody);
        return { data };
    } catch (error) {
        console.log("Error: ", error.response);
        return error.response;
    }
};



export { GetOrder, GetAllOrders, NewOrder, UpdateOrder, CancelOrder, UpdateOrderStatus };