import React, { useState, useEffect } from 'react';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';

import StorageInstance from "./../../../utils/storage";

import CountryList from "./../../../utils/country-list.json";
import Autocomplete from '@mui/material/Autocomplete';

const senderDetailValidations = {
    name: 'Enter sender name.',
    company: "Enter company name",
    //TODO: country: 'Enter country.',
    // Proof Doc
    proofId: 'Enter valid Proof Id',
    addressLine1: 'Enter Address Line 1',
    addressLine2: 'Enter Address Line 2',
    state: 'Enter State',
    postalCode: 'Enter Postal code.',
    phone: 'Enter phone number.',
    email: 'Enter Email Id',
};

const receiverDetailValidations = {
    name: 'Enter sender name.',
    company: "Enter company name",
    // TODO: country: 'Enter country.',
    addressLine1: 'Enter Address Line 1',
    addressLine2: 'Enter Address Line 2',
    state: 'Enter State',
    postalCode: 'Enter Postal code.',
    phone: 'Enter phone number.',
    email: 'Enter Email Id',
};


const AddressDetails = ({ saveAndContinueAddress, sender, receiver }) => {
    const [senderDetails, setSenderDetails] = useState(sender);
    const [receiverDetails, setReceiverDetails] = useState(receiver);

    // TODO: File preview
    const [selectedFile, setSelectedFile] = useState()
    const [preview, setPreview] = useState()

    const updateSenderDetails = (event) => {
        let { name, value } = event.target;
        setSenderDetails({ ...senderDetails, [name]: value });
    };

    const updateReceiverDetails = (event) => {
        let { name, value } = event.target;
        setReceiverDetails({ ...receiverDetails, [name]: value });
    };

    const handleFileInput = (event) => {
        console.log("Selected File:", event);
        if (!event.target.files || event.target.files.length === 0) {
            setSelectedFile(undefined)
            return
        }

        // I've kept this example simple by using the first image instead of multiple
        setSelectedFile(event.target.files[0])
    }

    useEffect(() => {
        if (!selectedFile) {
            setPreview(undefined)
            return
        }

        const objectUrl = URL.createObjectURL(selectedFile);
        setPreview(objectUrl)

        // free memory when ever this component is unmounted
        return () => URL.revokeObjectURL(objectUrl)
    }, [selectedFile]);

    // TODO:
    const uploadAction = (e) => {
        // const data = new FormData();
        // const imagedata = e.target.files[0];
        // data.append('inputname', imagedata);
    }

    const onHandleNext = () => {
        console.log("Sender details:", senderDetails);
        let canContinue = true;

        // 1. SENDER DETAILS VALIDATIONS
        Object.keys(senderDetailValidations).forEach((key, index) => {
            if ((!senderDetails[key]) || senderDetails[key] === "") {
                setSenderDetails(prevState => ({
                    ...prevState, [key]: "", error: true,
                    errorMessage: { ...prevState.errorMessage, [key]: senderDetailValidations[key] }
                }));
                canContinue = false;
            }
        });

        // 2. REVEIVER DETAILS VALIDATIONS
        Object.keys(receiverDetailValidations).forEach((key, index) => {
            if ((!receiverDetails[key]) || receiverDetails[key] === "") {
                setReceiverDetails(prevState => ({
                    ...prevState, [key]: "", error: true,
                    errorMessage: { ...prevState.errorMessage, [key]: receiverDetailValidations[key] }
                }));   
                canContinue = false;
            }
        });

        // 3. Continue if no errors.
        if (canContinue) {
            saveAndContinueAddress({senderDetails, receiverDetails});
        } else {
            console.log("Need to fill mandatory details.");
        }
    }

    return (
        <div style={{display: 'flex', flexDirection: 'row', justifyContent: 'space-around'  }}>
            
            <div className="sender-details" style={{ flex: 1, margin: '30px', maxWidth: 400 }}>
                <Typography variant="overline" display="block" gutterBottom style={{color: '#072753', fontWeight: '500' }}>
                    Sender
                </Typography>
                <div className="form-group">

                    {/* <TextField label="Name" name="name" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.name === ""} helperText={senderDetails?.name === "" ? senderDetails?.errorMessage?.name: ""}/><br /> */}

                    {/*  TODO: Should show only for logged in users.*/}
                    {
                        StorageInstance.isUserLoggedIn() && 
                        <React.Fragment>
                            <TextField label="Account No:" name="accountnum" disabled={true} margin="normal" size="small" fullWidth
                            value={"MAT110001"}/><br />
                        </React.Fragment>
                    }
                    
                    
                    <TextField label="Name *" name="name" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.name === ""}  value={senderDetails?.name}/><br />

                    <TextField label="Company Name *" name="company" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.company === ""}  value={senderDetails?.company}/><br />

                    <Autocomplete
                        //freeSolo
                        size="small"
                        onChange={(event, value) => {
                            console.log('country:', value);
                            setSenderDetails({ ...senderDetails, country: value['label'] });
                        }}
                        options={CountryList}
                        fullWidth // sx={{ width: 300 }}
                        renderInput={(params) => <TextField {...params} label="Country *" />}
                        name="country"
                        value={senderDetails?.country}
                        error={senderDetails?.country === ""}
                        />

                    <div style={{ display: 'flex', flexDirection: 'row', marginTop: 10, height: 80 }}>

                        {selectedFile ?  
                            <img src={preview} style={{ height: 80, width: 60, flex: 1, borderRadius: 5, objectFit: 'contain' }}/>:
                            <div style={{ height: 80, width: 60, flex: 1, borderRadius: 5, backgroundColor: '#f3f3f3', textAlign: 'center', paddingTop: 20  }}>Image Preview</div> 
                        }

                        <div style={{ marginTop: 10, width: 60, flex: 1, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                            <Button variant="outlined" component="label" size="small" style={{ width: 120 }} > Upload File
                                <input type="file" hidden accept="image/*" onChange={handleFileInput}/>
                            </Button>

                        </div>

                    </div>

                    <TextField label="ID Proof No *" name="proofId" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.proofId === ""}  value={senderDetails?.proofId}/><br />

                    <TextField label="Address Line 1 *" name="addressLine1" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.addressLine1 === ""} value={senderDetails?.addressLine1} /><br />

                    <TextField label="Address Line 2 *" name="addressLine2" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.addressLine2 === ""} value={senderDetails?.addressLine2} /><br />

                    <TextField label="State *" name="state" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.state === ""} value={senderDetails?.state} /><br />

                    <TextField label="Postal Code *" name="postalCode" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.postalCode === ""} value={senderDetails?.postalCode} /><br />

                    <TextField label="Phone Number *" name="phone" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.phone === ""} value={senderDetails?.phone} /><br />

                    <TextField label="Email Id *" name="email" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                        error={senderDetails?.email === ""} value={senderDetails?.email} /><br />
                </div>
            </div>

            <div id="receiver" className="tabcontent" style={{ flex: 1, margin: '30px', maxWidth: 400 }}>
                <Typography variant="overline" display="block" gutterBottom style={{color: '#072753', fontWeight: '500' }}>
                Receiver
                </Typography>
                <div className="form-group">
                    <TextField label="Name *" name="name" margin="normal" size="small" fullWidth onChange={updateReceiverDetails}
                        error={receiverDetails?.name === ""}  value={receiverDetails?.name}/><br />

                    <TextField label="Company Name:" name="company" margin="normal" size="small" fullWidth onChange={updateReceiverDetails}
                        error={receiverDetails?.company === ""}  value={receiverDetails?.company}/><br />

                    <Autocomplete
                        //freeSolo 
                        size="small"
                        fullWidth
                        name="country"
                        onChange={(event, value) => {
                            console.log('country:', value);
                            setReceiverDetails({ ...receiverDetails, country: value['label'] });
                        }}
                        options={CountryList}
                        value={receiverDetails?.country}
                        renderInput={(params) => <TextField {...params} label="Country *" />}
                        />

                    <TextField label="Address Line 1 *" name="addressLine1" margin="normal" size="small" fullWidth onChange={updateReceiverDetails}
                        error={receiverDetails?.addressLine1 === ""} value={receiverDetails?.addressLine1} /><br />

                    <TextField label="Address Line 2 *" name="addressLine2" margin="normal" size="small" fullWidth onChange={updateReceiverDetails}
                        error={receiverDetails?.addressLine2 === ""} value={receiverDetails?.addressLine2} /><br />

                    <TextField label="State *" name="state" margin="normal" size="small" fullWidth onChange={updateReceiverDetails}
                        error={receiverDetails?.state === ""} value={receiverDetails?.state} /><br />

                    <TextField label="Postal Code *" name="postalCode" margin="normal" size="small" fullWidth onChange={updateReceiverDetails}
                        error={receiverDetails?.postalCode === ""} value={receiverDetails?.postalCode} /><br />

                    <TextField label="Phone Number *" name="phone" margin="normal" size="small" fullWidth onChange={updateReceiverDetails}
                        error={receiverDetails?.phone === ""} value={receiverDetails?.phone} /><br />

                    <TextField label="Email Id *" name="email" margin="normal" size="small" fullWidth onChange={updateReceiverDetails}
                        error={receiverDetails?.email === ""} value={receiverDetails?.email} /><br />

                    <div style={{ display: 'flex', justifyContent: 'flex-end', marginTop: '30px' }}>
                        <Button variant="contained" endIcon={<NavigateNextIcon />} onClick={onHandleNext} >
                            Next
                        </Button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default AddressDetails;