import React, { useState } from 'react';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';

const loginDetailValidations = {
    username: 'Enter Username.',
    password: 'Enter Password.',
    confirmPassword: 'Password do not match.',
};

export const LoginInfo = React.forwardRef((props, ref) => {
    // TODO: 1. Do we need to check whther its exist or not?
    const [loginDetails, setLoginDetails] = useState({ });

    React.useImperativeHandle(ref, () => ({
        doValidation: doValidation
      }));

    const onLoginDetailsChange = (event) => {
        let { name, value } = event.target;
        setLoginDetails({ ...loginDetails, [name]: value });
        console.log('loginDetails:', loginDetails);
    }

    const doValidation = () => {
        let canContinue = true;

        // LOGIN DETAILS VALIDATIONS
        Object.keys(loginDetailValidations).forEach((key, index) => {
            if ((!loginDetails[key]) || loginDetails[key] === "") {

                if (key === 'confirmPassword'){
                    if (loginDetails['password'] !== "") {
                        setLoginDetails(prevState => ({
                            ...prevState, [key]: "", error: true,
                            errorMessage: { ...prevState.errorMessage, [key]: loginDetailValidations[key] }
                        }));
                    }
                } else {
                    setLoginDetails(prevState => ({
                        ...prevState, [key]: "", error: true,
                        errorMessage: { ...prevState.errorMessage, [key]: loginDetailValidations[key] }
                    }));
                }
                canContinue = false;
            }
        });

        return canContinue;
    }


    return (
        <React.Fragment>
            <Typography variant="overline" display="block" gutterBottom style={{color: '#072753', fontWeight: '700', textAlign: 'center', marginBottom: '30px' }}>
                Create your partner account.
            </Typography>

            <div className="login-details" style={{ display: 'flex', justifyContent: 'center', marginTop: '20px', marginBottom: '20px' }}>
                <div style={{ width: '30vw' }}>
                    <Typography variant="caption" display="block" gutterBottom style={{color: '#072753', fontWeight: '600' }}>
                        1. Create your login
                    </Typography>
                    <div className="form-group">

                        {/* <TextField label="Name" name="name" margin="normal" size="small" fullWidth onChange={updateSenderDetails}
                            error={senderDetails?.name === ""} helperText={senderDetails?.name === "" ? senderDetails?.errorMessage?.name: ""}/><br /> */}

                        <TextField label="Choose Username *" name="username" margin="dense" size="small" fullWidth  onChange={onLoginDetailsChange}
                            error={loginDetails?.username === ""} value={loginDetails?.username} /><br />

                        <TextField label="Choose Password *" name="password" margin="dense" size="small" fullWidth onChange={onLoginDetailsChange}
                            error={loginDetails?.password === ""} value={loginDetails?.password} type="password" /><br />

                        <TextField label="Confirm Password *" name="confirmPassword" margin="dense" size="small" fullWidth onChange={onLoginDetailsChange}
                            error={loginDetails?.confirmPassword !== ""  && loginDetails?.confirmPassword !== loginDetails?.password} 
                            helperText={loginDetails?.confirmPassword !== ""  && loginDetails?.confirmPassword !== loginDetails?.password ? loginDetails?.errorMessage?.confirmPassword: ""}
                            value={loginDetails?.confirmPassword} type="password" /><br />
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
});