import React, { useState } from 'react';
import { Outlet, useNavigate } from "react-router-dom";

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Button from '@mui/material/Button';

import PowerSettingsNewIcon from '@mui/icons-material/PowerSettingsNew';
import { LogoutUser } from "./../../services/login";
import StorageInstance from  "./../../utils/storage";
import { CONSTANTS, STATUSCODE } from './../../utils/constants';
import './layout.scss';


const Main = () => {
    const [loading, setLoading] = useState(false);
    const [openLogoutPopup, setOpenLogoutPopup] = useState(false);
    const navigate = useNavigate();

    const logout = async () => {
        setLoading(true);
        const { LoginID, Email } = JSON.parse(StorageInstance.getItem(CONSTANTS.userData));
        const { data } = await LogoutUser(LoginID, Email);
        console.log("After logout:", data);
        setLoading(false);
        if (data && data.ResultCode === STATUSCODE.LOGIN.SUCCESS) {
            // TODOL If possible, use store and use storage before reload.
            StorageInstance.removeItem(CONSTANTS.isLoggedIn);
            StorageInstance.removeItem(CONSTANTS.userData);
            navigate("/");
        } else {
            console.error("Error while logout");
        }
    }

    const handleOpen = () => {
        setOpenLogoutPopup(true);
    }

    const handleClose = () => {
        setOpenLogoutPopup(false);
    }

    return (
        <div className="main-content">
           <div className="header">
               <PowerSettingsNewIcon className="logout-button" onClick={handleOpen}/>
           </div>
           <div className="outlet-content">
                <Outlet />
           </div>

           <Dialog
                open={openLogoutPopup}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description">
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Are you sure, you want to logout?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Cancel</Button>
                    <Button onClick={logout} autoFocus variant="contained">OK</Button>
                </DialogActions>
            </Dialog>
        </div>
    );
};

export default Main;