import { HashRouter as Router, Routes, Route } from "react-router-dom";
import { QueryClient, QueryClientProvider } from 'react-query';
import { ReactQueryDevtools } from 'react-query/devtools';

import Login from "./pages/login/Login";
import Signup from "./pages/signup/Signup";
import TrackShipment from "./pages/tracking/TrackShipment";
import Layout from "./pages/layout/Layout";
import Dashboard from "./pages/dashboard/Dashboard";
import OrderList from "./pages/order/OrderList";
import NewOrder from "./pages/order/new-order/NewOrder";
import ErrorPage from "./pages/not-found/ErrorPage";
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.scss';

 // Create a client
 const queryClient = new QueryClient();

 //if (process.env.NODE_ENV === 'development') {
    const { createUsers, getUserDetails } = require('./mocks/users');
    console.log('Dev mode....');
    console.log('Created Users....', createUsers());
 //}

function App() {
  return (
    <QueryClientProvider client={queryClient}>
      <div className="App"> 
        <Router>
          <Routes>
            <Route path="/" element={<Login />} /> 
            <Route path="/signup" element={<Signup />} /> 
            <Route path="/track" element={<TrackShipment />} /> 
            <Route path="/home" element={<Layout />} >
              <Route index element={<Dashboard />} />
              <Route path="order" element={<OrderList />} />
              <Route path="neworder" element={<NewOrder />} />
            </Route>
            <Route path="*" element={<ErrorPage/>} />
          </Routes>  
        </Router>
    </div>

    <ReactQueryDevtools initialIsOpen={false} />
    </QueryClientProvider>
  );
}

export default App;
