import React, { useState, useEffect } from 'react';

import Autocomplete from '@mui/material/Autocomplete';
import TextField from '@mui/material/TextField';

import "./PreviewOrder.scss";

import CurrencyList from "./../../../utils/currency-list.json";
import { ParcelCharge } from "./../../../utils/constants";

const PreviewOrder = ({ senderDetails, receiverDetails, itemDetails, isDocument, selectedCurrency, onCurrencyChange, disableCurrenySelection = false }) => {
    const [currency, setCurrency] = useState(selectedCurrency);
    const parcelCharge = ParcelCharge;

    // TODO: Need to set selected currency by default.
    useEffect(() => {
        console.log('Selected currency:', selectedCurrency);
        // if (!!selectedCurrency) {
        //     setCurrency(selectedCurrency);
        // }
    }, []);

    let sender = [];
    sender = Object.keys(senderDetails);
    let receiver = []; 
    receiver = Object.keys(receiverDetails);

    const getDocumentWeightRate = (item) => {
        let rate = ParcelCharge.documentRate;

        if (item.weight >= 2) {
            rate = ParcelCharge.packageRate;
        }

        return item.weight * item.noOfPiece * rate;
    }

    const getPackageVolumeRate = (item) => {
        let rate = ParcelCharge.packageFixedRate;
        let volume = ((item.length * item.width * item.height)/ParcelCharge.dimensionDivider);
        if (volume >= 2) {
            rate = item.volume * item.itemsCount * ParcelCharge.packageRate;
        }

        return rate;
    }

    const getPackageWeightRate = (item) => {
        return item.weight * item.itemsCount * ParcelCharge.packageRate;
    }   

    return (
        // TODO: Need to make table as per Screenshot
        <div className="preview-order-comp">
            {/* style={{ 'height':  '340mm'}} */}
            <div className="flex-item-row" >
                <div className="sender">
                    <div>Sender Address:</div>
                    {
                        sender.map((key, index) => {
                            return <div>{ senderDetails[key]} </div>
                        })
                    }
                </div>
                <div className="receiver">
                    <div>Receiver Address:</div>
                    {
                        receiver.map((key, index) => {
                            return <div>{ receiverDetails[key]} </div>
                        })
                    }
                </div>
            </div>

            {/* style={{ 'height':  '340mm', marginTop:60 }} */}
            <div className="item-list" >
                <div>{ isDocument ? 'Document Details' : 'Package Items' } : </div>
                { isDocument ?
                    (itemDetails.length > 0 &&
                        <table className="item-list-table">
                            <thead className="table-header">
                                <tr>
                                    <th>S.NO</th>
                                    <th>WEIGHT (kg)</th>
                                    <th>NO.OF PIECES</th>
                                    <th>WEIGHT RATE</th>
                                    <th>NOTES</th>
                                </tr>
                            </thead>
                            <tbody className="table-body">
                                {
                                    itemDetails.map((item, index)=> {
                                        return <tr>
                                            <td>{index+1}</td>
                                            <td>{item.weight}</td>
                                            <td>{item.noOfPiece}</td>
                                            <td>{getDocumentWeightRate(item)}</td>
                                            <td>{item.notes}</td>
                                        </tr> 
                                    })
                                }
                            </tbody>
                        </table>)
                    :
                    (itemDetails?.length > 0 &&
                    <table className="item-list-table">
                        <thead className="table-header">
                            <tr>
                                <th>S.NO</th>
                                <th>WEIGHT</th>
                                <th>DIMENSION(LxWxH)</th>
                                <th>DECLARED VALUE</th>
                                <th>NO.OF ITEMS</th>
                                <th>VOLUME RATE</th>
                                <th>WEIGHT RATE</th>
                                <th>NOTES</th>
                            </tr>
                        </thead>
                        <tbody className="table-body">
                            {
                                itemDetails.map((item, index)=> {
                                    return <tr>
                                        <td>{index+1}</td>
                                        <td>{item.weight}</td>
                                        <td>{item.length}x{item.width}x{item.height}</td>
                                        <td>{item.decValue}</td>
                                        <td>{item.itemsCount}</td>
                                        <td>{getPackageVolumeRate(item)}</td>
                                        <td>{getPackageWeightRate(item)}</td>
                                        <td>{item.notes}</td>
                                    </tr> 
                                })
                            }
                        </tbody>
                    </table>)
                 }

                <div>


                <Autocomplete
                    //freeSolo 
                    value={currency}
                    size="small"
                    onChange={(event, value) => {
                        console.log(value);
                        setCurrency(value['label'] || "");
                        onCurrencyChange(value['label'] || "");
                    }}
                    options={CurrencyList}
                    sx={{ width: 300 }}
                    renderInput={(params) => <TextField {...params} label="Currency *" />}
                    name="currency"
                    // error={senderDetails?.country === ""}
                    style={{ marginTop: 30, marginBottom: 20 }}
                    disabled={disableCurrenySelection}
                    /><br />

                    <div style={{ backgroundColor: '#F1F0FF', padding: 10, paddingLeft: 20 }}>
                        <div style={{ fontSize: 12  , color: '#071f41', fontWeight: 600 }}>Charges :</div>
                        {/* Display the max of cumulative weight/Volume with chargeable rate <br /><br /> */}
                        <div style={{ fontSize: 10, color: '#071f41', fontWeight: 400, marginTop: 5 }}>
                            - Package fixed rate { ParcelCharge.packageFixedRate }, Package rate per kg { ParcelCharge.packageRate }.</div>
                        <div style={{ fontSize: 10, color: '#071f41', fontWeight: 400, marginTop: 5  }}>
                            - Document rate per kg { ParcelCharge.documentRate }, If weight exceeds 2kg then document parcel will be considered as package.</div>

                    </div>

                    {/* Invoice(Declared) value : get once at the end of package<br />
                        1.amount <br />
                        2.currency - we need select from list  */}
                 </div>
            </div>
        </div>
    )
};

export { PreviewOrder };